/* REACT */
import React from 'react';
import {TouchableOpacity, View, Text} from 'react-native';
/* UTILS */
import {COLORS} from '../../utils/theme';
/* STYLES */
import styles from './styles';

type Props = {
  amount: number;
  isEnabled: boolean;
  onChange: Function;
};

const Counter = ({amount, isEnabled, onChange}: Props) => {
  const onPlusPress = () => {
    onChange(amount + 1);
  };

  const onMinusPress = () => {
    if (amount !== 0) {
      onChange(amount - 1);
    }
  };

  return (
    <View
      style={[
        styles.container,
        !isEnabled ? {backgroundColor: COLORS.GRAY} : {},
      ]}>
      <TouchableOpacity onPress={onMinusPress} disabled={!isEnabled}>
        <Text style={styles.text}>-</Text>
      </TouchableOpacity>
      <Text style={styles.text}>{amount}</Text>
      <TouchableOpacity onPress={onPlusPress} disabled={!isEnabled}>
        <Text style={styles.text}>+</Text>
      </TouchableOpacity>
    </View>
  );
};

export default Counter;
